*{
  font-family: 'Nunito', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  background-color: #121418;
}

.sprints-container{
  display: flex;
  flex-direction: column;
  grid-gap: 2em;
  gap: 2em; 
  padding: 2em; ;
  align-items: center;
}

.sprint{
  max-width: 1010px;
  width: 100%; 
  background-color: #1B1F24;
  padding:2em; 
  border-radius: 8px; 
  display: flex;
  flex-direction: column;
  grid-gap: 1em;
  gap: 1em; 
}
.sprint h2{
  color: #ecf0f3; 
}

.select{
  display: grid;
  -webkit-appearance: none;
          appearance: none;
  padding: .2em;
  border-radius: 4px;
  margin: 0;
  color: #ecf0f3; 
  width: 100%;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  outline: none;
  cursor: pointer;
  background-image: url(/Sprints-Socio-Technology-Interaction/static/media/arrow.e351afa2.svg);
  background-repeat: no-repeat;
  background-position: right 10px top 50%;
  background-color: #1B1F24;
  border: 1px solid #5E646B;
}

.select option{
  color: #ecf0f3; 
}

button{
  padding: .3em .5em; 
  border-radius: 8px;
	background: #0DC68A;
	border: none;
	font: inherit;
  font-weight: bold;
	cursor: pointer;
	outline: inherit;
  
}


.important{
  color: #F6A513; 
}

.sprint1 form{
  display: flex;
  justify-content: space-between;
}
.sprint1 form button{
  width: 30%
}
.sprint1 form select{
  width: 30%; 
}

.sprint2__info{
  display: flex;
  justify-content: space-between;
  grid-gap: .5em;
  gap: .5em; 

}
.sprint2__result{
  display: flex;
  flex-direction: column; 
  flex-grow: 1;
  justify-content: center; 
  align-items: center;
}
.sprint2__actions{
  height: 100%; 
  display: flex; 
  flex-direction: column;
  grid-gap: 1em;
  gap: 1em; 
  width: 50%; 
}

.sprint3 form{
  display: flex;
  justify-content: space-between; 
  align-items: flex-end;
}

.sprint3 form label{  
  display: flex; 
  grid-gap: .5em; 
  gap: .5em;
  flex-direction: column; 
  width: 30%; 
  justify-content: space-between; 
}

.sprint3 form label span, .sprint4 form aside label span{
  color: #5E646B; 
}

.input-number{
  color: #ecf0f3; 
  padding: .2em;
  border-radius: 4px;
  background-color: #1B1F24;
  font-size: 16px;
  border: 1px solid #5E646B;
  padding-left: .5em;
  outline: none; 
}

.sprint3 form button{
  width: 30%;
  height: 32.1px; 
}

.sprint3 h3{
  color: #F6A513; 
}

.sprint3 ul, .sprint4 ul{
  color: #ecf0f3; 
  display: flex;
  flex-direction: column; 
  grid-gap: 1em; 
  gap: 1em; 
  list-style: none;
}

.sprint4__interaction{
  display: flex;
  grid-gap: 1em;
  gap: 1em; 
  width: 100%; 
}

.sprint4__interaction > *{
  border: 1px solid #5E646B; 
  padding: 1em; 
  border-radius: 8px;
}

.slider-container{
  flex-grow: .5;
  height: -webkit-min-content;
  height: min-content;
  overflow-y: auto; 
  display: flex;
  flex-direction: column; 
  grid-gap: .5em; 
  gap: .5em; 

}

.sprint4__actions{
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  grid-gap: 1em;
  gap: 1em;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content; 
}

.sprint4__actions label{
  display: flex; 
  flex-direction: column; 
  grid-gap: .5em; 
  gap: .5em;
}

.sprint4__form{
  display: flex; 
  flex-direction: column;
  grid-gap: 1em;
  gap: 1em; 
}

.btn-container{
  border: 1px solid #0DC68A; 
  padding: 1em ;
  display: flex;
  border-radius: 8px;
  justify-content: space-between;
}

.sprint4__submit{
  width: 200px;
}

.sprint4__set-info-container{
  display: flex;
  grid-gap: 1em;
  gap: 1em;
  align-items: center; 
}

.sprint4__set-info{
  background-color: #212930;
  padding: .3em 1em; 
  border-radius: 1000px;
  display: flex;
  grid-gap: 1em;
  gap: 1em; 
}

.sprint4__set-info span{
  color: #8d939b;
}
.sprint4__set-info img{
  height: 18px; 
}


